.about {
  display: flex;
  padding-top: 120px;
  padding-bottom: 120px;
  box-sizing: border-box;
  background-color: $light-grey;
  font-size: 32px;
  line-height: 48px;

  a {
    color: $blue;
    text-decoration: none;
  }
}

.about-info {
  position: relative;
  display: block;
  margin: 0 auto;
  margin-top: 60px;
  margin-bottom: 68px;
  width: 960px;
  text-align: center;
}

.about-info::before {
  content: "";
  display: block;
  position: absolute;
  left: 430px;
  top: -55px;
  width: 120px;
  height: 15px;
  background-image: url("../img/curl1.svg");
  background-repeat: no-repeat;
}

.about-info::after {
  content: "";
  display: block;
  position: absolute;
  left: 430px;
  top: 200px;
  width: 120px;
  height: 15px;
  background-image: url("../img/curl2.svg");
  background-repeat: no-repeat;
  transform: rotate(180deg);
}

@media screen and (max-width: 768px) {
  .about {
    width: 768px;
  }

  .about-info {
    width: 600px;
    margin-top: 40px;
    margin-bottom: 48px;
  }

  .about-info::before {
    left: 240px;
    top: -45px;
  }

  .about-info::after {
    left: 240px;
    top: 270px;
  }
}

@media screen and (max-width: 320px) {
  .about {
    display: none;
  }
}
